import React from "react";
import styled from "styled-components";
import { IVendorBox } from "./VendorBox";

const Container = styled.div`
    position: absolute;
    top: -4px;
    right: -4px;
`;

const VendorImage = styled.img`
    width: 16px;
`;

export const VendorBox: React.FunctionComponent<IVendorBox.IProps> = props => {
    return <Container>
        {
            props.vendorType === "INSTAGRAM" &&
                <VendorImage src="/static/images/logo_insta@2x.png" alt="Instagram Logo" />
        }
        {
            props.vendorType === "TIKTOK" &&
                <VendorImage src="/static/images/logo_tiktok@2x.png" alt="TIKTOK Logo" />
        }
    </Container>
};
