import React from "react";
import styled from "styled-components";

import { IFeedAuthor } from "./Author";
import { VendorBox } from "../VendorBox";

const Container = styled.div`
    margin: 20px 16px;
    position: absolute;
    display: flex;
    width: 100%;
    bottom: 0;
`;

const ProfileImageBoxWrapper = styled.div`
    position: relative;
`;

const ProfileImageBox = styled.div`
    flex: none;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
`;

const ProfileImage = styled.img`
    display: inline;
    margin: 0 auto;
    height: auto;
    width: 100%;
`;

const ProfileNameBox = styled.ul`
    margin-left: 20px;
`;

const ProfileDetail = styled.li`
    color: #ffffff;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
    font-size: 14px;
`;

export const Author: React.FunctionComponent<IFeedAuthor.IProps> = props => {
      return <Container>
                <ProfileImageBoxWrapper>
                    <ProfileImageBox>
                        {props.profileImgUrl && <ProfileImage src={props.profileImgUrl} alt={""}/>}
                    </ProfileImageBox>
                    <VendorBox vendorType={props.vendorType} />
                </ProfileImageBoxWrapper>
                <ProfileNameBox>
                    <ProfileDetail>
                        {props.name}
                    </ProfileDetail>
                    <ProfileDetail>
                        {props.postedAt}
                    </ProfileDetail>
                </ProfileNameBox>
      </Container>
};
