// #region Global Imports
import * as React from "react";
import { NextPage } from "next";
import { useSelector, useDispatch } from "react-redux";
import Head from "next/head";
// @ts-ignore
import MagicGrid from "react-magic-grid";
// #endregion Global Imports

// #region Local Imports
import {
    Container,
    Top,
    TopText,
    Middle,
    MiddleLeft,
    MiddleLeftButtons,
    MiddleRight,
} from "@Styled/Home";
import { IStore } from "@Redux/IStore";
import { HomeActions } from "@Actions";
import { HashTagButton, Feed } from "@Components";
// #endregion Local Imports

// #region Interface Imports
import { IHomePage, ReduxNextPageContext } from "@Interfaces";
// #endregion Interface Imports

const Home: NextPage<IHomePage.IProps, IHomePage.InitialProps> = ({}) => {
    const home = useSelector((state: IStore) => state.home);
    const dispatch = useDispatch();

    const renderHashTagButtons = (activeTag: String) =>
        home.hashTags.map(tag => (
            <HashTagButton
                key={tag}
                tag={tag}
                isActive={activeTag === tag}
                onClick={() => dispatch(HomeActions.GetPost({ key: tag }))}
            />
        ));

    const renderFeeds = () =>
        home.posts.map((feed, i) => (
            <Feed
                key={i}
                vendorType={feed.vendorType}
                tags={feed.tags}
                title={feed.title}
                postedAt="2020.02.02"
                author={{
                    name: feed.author.name,
                    profileImgUrl: feed.author.profileImageUrl,
                }}
                content={{
                    thumbnailUrl: feed.content.thumbnailImageUrl,
                    originUrl: feed.content.originUrl,
                    link: feed.content.link,
                    type: feed.content.type,
                }}
            />
        ));

    return (
        <Container>
            <Head>
                <title>PetKoKo</title>
            </Head>
            <Top />
            <Middle>
                <MiddleLeft>
                    <MiddleLeftButtons>
                        {renderHashTagButtons(home.activeTag)}
                    </MiddleLeftButtons>
                </MiddleLeft>
                <MiddleRight>
                    <TopText>{`#${home.activeTag}`}</TopText>
                    {home.posts && home.posts.length > 0 && (
                        <MagicGrid
                            items={home.posts.length}
                            gutter={16}
                        >
                            {renderFeeds()}
                        </MagicGrid>
                    )}
                </MiddleRight>
            </Middle>
        </Container>
    );
};

Home.getInitialProps = async (
    ctx: ReduxNextPageContext
): Promise<IHomePage.InitialProps> => {
    await ctx.store.dispatch(await HomeActions.GetHashTag({ key: "koko" }));
    await ctx.store.dispatch(
        await HomeActions.GetPost({ key: ctx.store.getState().home.activeTag })
    );
    return {};
};

export default Home;
