// #region Global Imports
import { Dispatch } from "redux";
// #endregion Global Imports

// #region Local Imports
import { ActionConsts } from "@Definitions";
import { PostService } from "@Services";
// #endregion Local Imports

// #region Interface Imports
import { IHomePage } from "@Interfaces";
// #endregion Interface Imports

export const HomeActions = {
    Map: (payload: {}) => ({
        payload,
        type: ActionConsts.Home.SetReducer,
    }),

    Reset: () => ({
        type: ActionConsts.Home.ResetReducer,
    }),

    GetHashTag: (payload: IHomePage.Actions.IHashTagPayload) => async (
        dispatch: Dispatch
    ) => {
        const results = await PostService.GetHashTag({
            params: { key: payload.key },
        });

        dispatch({
            payload: {
                hashTags: results.content,
                activeTag: results.content[0],
            },
            type: ActionConsts.Home.SetReducer,
        });

        return results.content;
    },
    GetPost: (payload: IHomePage.Actions.IPostPayload) => async (
        dispatch: Dispatch
    ) => {
        const results = await PostService.GetPost({
            params: { key: payload.key },
        });

        dispatch({
            payload: {
                posts: results.content.filter(post =>
                    post.tags.find(tag => tag === payload.key)
                ),
                activeTag: payload.key,
            },
            type: ActionConsts.Home.SetReducer,
        });
    },
};
