import * as React from "react";
import styled from "styled-components";
import { IHashTagButton } from "./HashTagButton";

import { Button } from "../Basic";

const Container = styled(Button)`
    border-radius: 20px;
    padding: 7px 15px;
`;

export const HashTagButton: React.FunctionComponent<IHashTagButton.IProps> = ({
    tag,
    isActive,
    onClick,
}) => {
    return (
            <Container className={isActive ? "active" : ""} onClick={onClick}>
                #{tag}
            </Container>
    );
};
